/* 圖片寬度 */
/* topSlider是在jsx最外層自定的div */
.topSlider .slick-slider {
  width: 90%;
  margin: 0 auto;
}

/* 圖片高度 (topPic是包裹img的div) */
.topSlider .topPic {
  height: 500px;
}

/* 圖片樣式 */
.topSlider img {
  height: 100%;
  object-fit: cover;
  opacity:.9
}

@media screen and (min-width: 992px) {
  /* 調整大小 */
  .topSlider .slick-slider {
    width: 75%;
    /* 讓圖片靠右 */
    margin: 0 0 0 auto;
  }

  /* 高度調高 */
  .topSlider .topPic {
    height: 1000px;
  }
 
  }

/* Arrows */
.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;

  position: absolute;
  top: 50%;

  display: block;
  /* 實際的按鈕大小 (點擊後可以跳上下一張的範圍)*/
  width: 0px;
  height: 0px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);

  cursor: pointer;

  color: transparent;
  border: none;
  outline: none;
  background: transparent;
}

/* 預設的按鈕 */
/* 是文字類型 */
.slick-prev:before,
.slick-next:before {
  font-family: 'slick';
  /* 如果要自訂按鈕樣式就把這個隱藏起來 */
  /* 大小 顏色 調整 */
  font-size: 0px;
  line-height: 1;

  opacity: .75;
  color: #000;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* 按鈕的位置 */
.slick-prev {
  left: 50%;
  z-index: 2;
}

[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto;
}

.slick-prev:before {
  content: '←';
}

[dir='rtl'] .slick-prev:before {
  content: '→';
}

.slick-next {
  right: 50%;
}

/* 按鈕的位置 */
[dir='rtl'] .slick-next {
  right: auto;
  left: -25px;
}

.slick-next:before {
  content: '→';
}

[dir='rtl'] .slick-next:before {
  content: '←';
}

